<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="mx-auto elevation-2">
        <v-card-text>
          <!--<v-window>
            <v-window-item
              key="card-1"
            >
              <v-card
              color="white"
              elevation="2"
              >
              <v-row
                  class="fill-height"
                  align="center"
                  justify="center"
                >
                  <v-col cols="12">
                    <span class="justify-center error--text">
                      Se debe subir los archivos con mucho cuidado.
                    </span>
                  </v-col>
                </v-row>
              </v-card>
            </v-window-item>
          </v-window>
          <br>-->
          <span class="headline justify-center black--text font-weight-bold">
            Información General
            <v-btn
              icon
              color="primary"
              @click="abrirEdicionRef"
              v-if="editOk"
            >
              <v-icon>edit</v-icon>
            </v-btn>
          </span>
          <br>
          <span class="subheading font-weight-bold">
            <v-list dense>
              <v-list-item>
              <v-list-item-content>Fecha Límite de Entrega:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ itemactual.fecha_cierre_format }}
              </v-list-item-content>
              </v-list-item>
              <v-list-item>
              <v-list-item-content>Tiempo Faltante:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ `${tiempoTrascurrido.days} Dias, ${tiempoTrascurrido.hours} Horas, ${tiempoTrascurrido.minutes} Minutos, ${tiempoTrascurrido.seconds} Segundos` }}
              </v-list-item-content>
              </v-list-item>
              <v-list-item>
              <v-list-item-content>Estado:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ itemactual.estado }}
              </v-list-item-content>
              </v-list-item>
            </v-list>
          </span>
          <span class="headline justify-center black--text font-weight-bold">
            Documentos Generales
          </span>
          <br>
          <div style="padding-bottom:10px" v-if="editOk">
            <v-btn fab dark small color="primary" @click="limpiarArchivosAdd" >
              <v-icon dark>add</v-icon>
            </v-btn>
          </div>
          <v-data-table
            :headers="headers"
            :items="arrDocs"
            class="elevation-1"
          >
            <template v-slot:item.descargar="{ item }">
              <v-icon
                class="mr-5"
                color="primary"
                @click="downloadFile(item.file_path, item.file_name)"
                v-if="item.file_name != '' && item.file_path != ''"
              >
                archive
              </v-icon>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
     </v-flex>
     <v-dialog
      v-model="dialog_editar"
      persistent
      max-width="600"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center blue--text">
          Editar
        </v-card-title>
        <v-card-text>
          <editRef
            :fecha_cierre.sync = "itemactual.fecha_cierre"
            :estado.sync = "itemactual.estado"
            @actualizaDatos="actualizarDatos"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="dialog_editar=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog_subiradjgeneral"
      persistent
      max-width="600"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center blue--text">
          Agregar Documento General
        </v-card-title>
        <v-card-text>
          <v-row>
            <v-col>
              <v-text-field
                label="Documento"
                v-model="nombreDocumento"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <AddFilesToStorage
                :item="arcDocumento"
                placeholder="Cargar Archivo"
                dense
                hide-details
                prepend-icon
                @remove-file="fileReset++"
                :key="fileReset"
                @file-changed="fileReset++"
              />
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="guardarDocGeneral">Guardar</v-btn>
          <v-btn color="primary" dark text @click="dialog_subiradjgeneral=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
   </v-container>
</template>
<script>
import { adminApp, anunciosProcAdquisicion } from '@/services/api'
import AddFilesToStorage from '@/components/shared/AddFilesToStorage'
import axios from 'axios'
export default {
  name: 'anunciosTabRef',
  props: ['itemactual'],
  components: {
    editRef: () => import('./editReferencia'),
    AddFilesToStorage
  },
  data () {
    return {
      tiempoTrascurrido: [],
      dialog_editar: false,
      dialog_subiradjgeneral: false,
      fileReset: 0,
      nombreDocumento: '',
      arcDocumento: [],
      editOk: 0,
      arrDocs: [
        { id: 1, doc: 'Términos de Referencia' },
        { id: 2, doc: 'Anexo Persona Natural' },
        { id: 3, doc: 'Formato de oferta económica' },
        { id: 4, doc: 'Información financiera, estados financieros' },
        { id: 5, doc: 'Anexo Fraude y Anticorrupción' }
      ],
      headers: [
        // { text: 'Id', value: 'id' },
        { text: 'Documento', value: 'descripcion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Descargar Doc.', value: 'descargar', align: 'center', class: 'subtitle-1 font-weight-bold black--text' }
      ]
    }
  },
  methods: {
    getTimeRemaining (endtime) {
      const total = Date.parse(endtime) - Date.parse(new Date())
      const seconds = Math.floor((total / 1000) % 60)
      const minutes = Math.floor((total / 1000 / 60) % 60)
      const hours = Math.floor((total / (1000 * 60 * 60)) % 24)
      const days = Math.floor(total / (1000 * 60 * 60 * 24))
      this.tiempoTrascurrido = { total, days, hours, minutes, seconds }
    },
    async buscarDocsTermRef () {
      const { data } = await anunciosProcAdquisicion.traerDocumentosGenerales({
        id_termref: this.itemactual.id_termref
      })
      this.arrDocs = data.termref_adjuntos
    },
    limpiarArchivosAdd () {
      this.nombreDocumento = ''
      this.arcDocumento = []
      this.dialog_subiradjgeneral = true
    },
    downloadFile (url, nombreArchivo) {
      axios({
        method: 'get',
        url: url,
        responseType: 'arraybuffer'
      })
        .then(response => {
          this.forceFileDownload(response, nombreArchivo)
        })
        .catch(() => console.log('error occured'))
    },
    forceFileDownload (response, nombreArchivo) {
      const url = window.URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', nombreArchivo) // or any other extension
      document.body.appendChild(link)
      link.click()
    },
    abrirEdicionRef () {
      this.dialog_editar = true
    },
    async actualizarDatos (datos) {
      const { data } = await adminApp.actualizapropuestaConvocatoria({
        idpropuestaconvocatoria: this.itemactual.id,
        fecha_cierre: datos.fecha_cierre,
        estado: datos.estado
      })
      if (data.respuesta) {
        const formatFechaCierre = datos.fecha_cierre.split('-')
        let dia = formatFechaCierre[2].toString()
        if (dia.length > 2) {
          dia = formatFechaCierre[2].toString().substring(0, 2)
        } else {
          dia = formatFechaCierre[2].toString()
        }
        const fechaCierre = dia + '/' + formatFechaCierre[1].toString() + '/' + formatFechaCierre[0].toString()
        this.$toasted.success('Se actualizarón los datos con éxito', { icon: 'done', duration: 5000 })
        this.itemactual.fecha_cierre = datos.fecha_cierre
        this.itemactual.fecha_cierre_format = fechaCierre
        this.itemactual.estado = datos.estado
        this.dialog_editar = false
      } else {
        this.$toasted.error('Se presentó error al intentar de actualizar los datos', { icon: 'error', duration: 5000 })
      }
    },
    async guardarDocGeneral () {
      const dataenv = { doc_solicitado: this.nombreDocumento, file_name: this.arcDocumento.file_name, file_path: this.arcDocumento.file_path, file_size: this.arcDocumento.file_size, file_type: this.arcDocumento.file_type }
      const { data } = await adminApp.adicionarDocumentosGenerales({
        id_termref: this.itemactual.id_termref,
        data: dataenv
      })
      if (data.respuesta) {
        this.buscarDocsTermRef()
        this.$toasted.success('Se agregó el Adjunto con éxito', { icon: 'done', duration: 5000 })
        this.dialog_subiradjgeneral = false
      } else {
        this.$toasted.error('Se presentó error al intentar de actualizar los datos', { icon: 'error', duration: 5000 })
      }
    }
  },
  watch: {
    itemactual: {
      handler: function (val) {
        if (val) {
          this.editOk = this.$session.get('codigopersonal') === val.ab_asignado
          this.getTimeRemaining(this.itemactual.fecha_cierre)
          this.buscarDocsTermRef()
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
    this.interval = setInterval(() => {
      this.getTimeRemaining(this.itemactual.fecha_cierre)
    }, 1000)
    this.buscarDocsTermRef()
  }
}
</script>
